import styled from "styled-components";
interface Props {
  bg: string;
}
export const UpButton = styled.button<Props>`
  padding: 8px 16px;
  background: ${({ bg }: any) => bg || "black"};
  border-radius: 10px;
  height: 54px;
  font-family: "source-sans-3";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  color: #ffffff;
  margin: 0px 10px;
  border: none;

  &:hover {
    cursor: pointer;
  }
`;
