import { AxiosCall } from "./../models";
import { AxiosResponse } from "axios";
import { useEffect, useState } from "react";
import useFeedback from "./useFeedback";

const useFetchAndLoad = () => {
  const [loading, setLoading] = useState(false);
  let controller: AbortController;
  const { showPopup } = useFeedback();
  const callEndpoint = async (axiosCall: AxiosCall<any>, successCallback: (data: any) => void) => {
    if (axiosCall.controller) controller = axiosCall.controller;
    setLoading(true);
    let result = {} as AxiosResponse<any>;
    try {
      result = await axiosCall.call;
    } catch (err: any) {
      setLoading(false);
      showPopup("Oh no!", "There was an unexpected error, please try again");
      throw err;
    }
    setLoading(false);
    console.log(result.data.data);
    switch (result.data.statusCode) {
      case 200:
        successCallback(result.data.data);
        break;
      case 400:
        showPopup("Oh no!", result.data.error);
        break;
      default:
        showPopup("Oh no!", result.data.error);
        break;
    }

    return result;
  };

  const cancelEndpoint = () => {
    setLoading(false);
    controller && controller.abort();
  };

  useEffect(() => {
    return () => {
      cancelEndpoint();
    };
  }, []);

  return { loading, callEndpoint };
};

export default useFetchAndLoad;
