import { createSlice } from "@reduxjs/toolkit";
import { ProfileInterface } from "../../models/interfaces";

export const ProfileEmptyState: ProfileInterface = {
  location: "",
  profession: "",
  token_benefits: "",
  events_held: "",
  description: "",
  social_media: [{ name: "", link: "" }],
  image: "",
};

export const profileSlice = createSlice({
  name: "profile",
  initialState: ProfileEmptyState,
  reducers: {
    newProfile: (state, action) => action.payload,
    modifyProfile: (state, action) => ({ ...state, ...action.payload }),
  },
});

export const { newProfile, modifyProfile } = profileSlice.actions;

export default profileSlice.reducer;
