import { Box, Button, Card, CardContent, Divider, Grid, Typography } from "@mui/material";
import { useSelector } from "react-redux";
import { useFetchAndLoad } from "../../hooks";
import { UserInterface } from "../../models/interfaces";
import { TokenInterface } from "../../models/interfaces/Token.interface";
import { AppStore } from "../../redux/store";
import { setDefaultToken } from "../../services";

export const TokenCard = (tokenProp: TokenInterface) => {
  const { loading, callEndpoint } = useFetchAndLoad();
  const userState: UserInterface = useSelector((store: AppStore) => store.user);

  const handleEditDefault = async () => {
    const data = { id: tokenProp.id };
    await callEndpoint(setDefaultToken(JSON.stringify(data), userState.token), successCallback);
  };

  const successCallback = (data: any) => {
    window.location.reload();
  };

  return (
    <Card
      sx={{
        display: "flex",
        flexDirection: "column",
        height: "100%",
      }}
    >
      <CardContent sx={{ pb: 0 }}>
        <Box sx={{ p: 2 }}>
          <Typography align="center" color="textPrimary" gutterBottom variant="h5">
            {tokenProp.name} ({tokenProp.symbol})
          </Typography>
          <Typography align="center" color="textPrimary" variant="body1">
            <a target="_blank" href={"https://testnet.bscscan.com/address/" + tokenProp.address}>
              {tokenProp.address}
            </a>
          </Typography>
          <Typography
            align="center"
            color="textPrimary"
            variant="body1"
            sx={{
              overflow: "hidden",
              textOverflow: "ellipsis",
              display: "-webkit-box",
              WebkitLineClamp: "2",
              WebkitBoxOrient: "vertical",
            }}
          >
            {tokenProp.description}
          </Typography>
        </Box>
        <Divider />
        <Box sx={{ p: 2 }}>
          <Grid container spacing={2} sx={{ justifyContent: "space-between" }}>
            <Grid
              item
              sx={{
                alignItems: "center",
                display: "flex",
              }}
            >
              <Typography color="textSecondary" display="inline" sx={{ pl: 1 }} variant="body2">
                {tokenProp.imported ? <span>Imported</span> : <span>UpSpore Token</span>}
              </Typography>
            </Grid>
            <Grid
              item
              sx={{
                alignItems: "center",
                display: "flex",
              }}
            >
              {tokenProp.default ? (
                <Button>Default token</Button>
              ) : (
                <Button onClick={handleEditDefault}>{loading ? <span>Updating</span> : "Set as default"}</Button>
              )}
            </Grid>
          </Grid>
        </Box>
      </CardContent>
      <Box sx={{ flexGrow: 1 }} />
    </Card>
  );
};
