import { useEffect, useState } from "react";
import { Box, Button, Card, CardContent, CardHeader, Divider, Grid, TextField } from "@mui/material";
import { TokenInterface, UserInterface } from "../../models/interfaces";
import { AppStore } from "../../redux/store";
import { useDispatch, useSelector } from "react-redux";
import { useFetchAndLoad, useWalletConnection } from "../../hooks";
import { useNavigate } from "react-router-dom";
import { createUserToken, updateWallet } from "../../services";
import useDeployToken from "../../hooks/useDeployToken";

export const CreateTokenForm = (props: any) => {
  const navigate = useNavigate();
  const userState: UserInterface = useSelector((store: AppStore) => store.user);
  const dispatch = useDispatch();
  const { loading, callEndpoint } = useFetchAndLoad();
  const { connectWallet, connectedWallet, connectButtonText, web3Provider } = useWalletConnection();
  const { deployToken } = useDeployToken();
  const [tokenName, setTokenName] = useState("");
  const [tokenSymbol, setTokenSymbol] = useState("");
  const [tokenDescription, setTokenDescription] = useState("");

  const returnToTokensPanel = () => {
    navigate("/creator-tokens");
  };

  const createTokenSuccessCallback = (data: any) => {
    const tokenData: TokenInterface = data;
    if (tokenData) {
      returnToTokensPanel();
    }
  };
  const updateWalletInDatabase = async (_wallet: string) => {
    const walletData = { wallet: _wallet };
    await callEndpoint(updateWallet(JSON.stringify(walletData), userState.token), () => {
      console.log("wallet updated");
    });
  };
  const handleDeployContract = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (connectedWallet != "" && web3Provider != undefined) {
      await updateWalletInDatabase(connectedWallet);
      const deployedAddress = await deployToken(web3Provider, connectedWallet, tokenName, tokenSymbol);
      console.log("deployed: " + deployedAddress);
      if (deployedAddress != "") {
        const formData = new FormData();
        formData.append("name", tokenName);
        formData.append("symbol", tokenSymbol);
        formData.append("address", deployedAddress);
        formData.append("description", tokenDescription);

        const formattedData = JSON.stringify(Object.fromEntries(formData));
        await callEndpoint(createUserToken(formattedData, userState.token), createTokenSuccessCallback);
      }
    }else{
      console.log("no connected wallet or web provider");
    }
  };

  return (
    <form autoComplete="off" noValidate {...props} onSubmit={handleDeployContract}>
      <Grid container spacing={3}>
        <Grid item lg={8} md={6} xs={12}>
          <Card>
            <CardHeader
              subheader="This will deploy a BEP20 contract on the BSC blockchain. You will be able to edit only the description of the token after deployment"
              title="Token"
            />
            <Divider />
            <CardContent>
              <Grid container spacing={3}>
                <Grid item md={12} xs={12}>
                  <TextField
                    fullWidth
                    helperText="Name your token, try to be unique.."
                    label="Token Name"
                    name="token-name"
                    required
                    value={tokenName}
                    variant="outlined"
                    onChange={(e) => setTokenName(e.target.value)}
                  />
                </Grid>
                <Grid item md={12} xs={12}>
                  <TextField
                    fullWidth
                    helperText="Choose a nice symbol for your token, try to be unique to avoid confusion for your followers. (ex: BNB)"
                    label="Token Symbol"
                    name="token-symbol"
                    required
                    value={tokenSymbol}
                    variant="outlined"
                    onChange={(e) => setTokenSymbol(e.target.value)}
                  />
                </Grid>
                <Grid item md={12} xs={12}>
                  <TextField
                    fullWidth
                    helperText="Please describe your token and it's use cases."
                    label="Description"
                    name="description"
                    rows={6}
                    required
                    multiline
                    value={tokenDescription}
                    variant="outlined"
                    onChange={(e) => setTokenDescription(e.target.value)}
                  />
                </Grid>
              </Grid>
            </CardContent>
            <Divider />
            <Box
              sx={{
                display: "flex",
                justifyContent: "flex-end",
                p: 2,
              }}
            >
              {connectedWallet ? (
                <Button type="submit" color="primary" variant="contained">
                  {loading ? <span>Updating...</span> : <span>Deploy token contract</span>}
                </Button>
              ) : (
                <Button sx={{ marginRight: 2 }} color="primary" variant="outlined" onClick={connectWallet}>
                  {connectButtonText}
                </Button>
              )}
            </Box>
          </Card>
        </Grid>
      </Grid>
    </form>
  );
};
