import axios from "axios";
import { API_BASE_URL, loadAbort } from "./../utilities";

const baseUrl: string = API_BASE_URL;

export const createUserToken = (data: string, authToken: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
    Authorization: "Bearer " + authToken,
  };
  return {
    call: axios.post(baseUrl + "token", data, { headers: headers, signal: controller.signal }),
    controller,
  };
};

export const getUserTokens = (authToken: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
    Authorization: "Bearer " + authToken,
  };
  return {
    call: axios.get(baseUrl + "token", { headers: headers, signal: controller.signal }),
    controller,
  };
};
export const setDefaultToken = (data: string, authToken: string) => {
  const controller = loadAbort();
  console.log(data);
  const headers = {
    "Content-Type": "application/json",
    Authorization: "Bearer " + authToken,
  };
  return {
    call: axios.post(baseUrl + "token/setDefaultToken", data, { headers: headers, signal: controller.signal }),
    controller,
  };
};
