import { createSlice } from "@reduxjs/toolkit";
import { UserInterface } from "../../models/interfaces";

export const UserEmptyState: UserInterface = {
  token: "",
  user: {
    email: "",
    name: "",
    type: 0,
    username: "",
    phone: 0,
    wallet: "",
  },
};

export const userSlice = createSlice({
  name: "user",
  initialState: UserEmptyState,
  reducers: {
    createUser: (state, action) => action.payload,
    modifyUser: (state, action) => ({ ...state, ...action.payload }),
    setCredentials: (state, action) => ({ ...state, token: action.payload.authToken }),
    logOut: () => UserEmptyState,
  },
});

export const { createUser, modifyUser, setCredentials, logOut } = userSlice.actions;

export default userSlice.reducer;
