import { createSlice } from "@reduxjs/toolkit";
import { SnackBarInterface } from "../../models/interfaces";

export const SnackBarEmptyState: SnackBarInterface = {
  type: "",
  message: "",
  opened: false,
};

export const snackbarSlice = createSlice({
  name: "snackbar",
  initialState: SnackBarEmptyState,
  reducers: {
    modifySnackBar: (state, action) => ({ ...state, ...action.payload }),
    resetSnackBar: (state, action) => SnackBarEmptyState,
  },
});

export const { modifySnackBar, resetSnackBar } = snackbarSlice.actions;

export default snackbarSlice.reducer;
