import { useEffect, useState } from "react";
import {
  Box,
  Button,
  Card,
  CardActions,
  CardContent,
  CardHeader,
  Divider,
  Grid,
  IconButton,
  MenuItem,
  TextField,
  Typography,
} from "@mui/material";
import { UserInterface } from "../../models/interfaces";
import { AppStore } from "../../redux/store";
import { useSelector } from "react-redux";
import { useFetchAndLoad } from "../../hooks";
import { getAccountData, saveAccountData } from "../../services";
import useFeedback from "../../hooks/useFeedback";
import EditIcon from "@mui/icons-material/Edit";

export const CreatorPaymentForm = (props: any) => {
  const userState: UserInterface = useSelector((store: AppStore) => store.user);
  const { loading, callEndpoint } = useFetchAndLoad();
  const [bankNumber, setBankNumber] = useState("");
  const [bankName, setBankName] = useState("");
  const [holder, setHolder] = useState("");
  const [documentType, setDocumentType] = useState("");
  const [iban, setIban] = useState("");
  const [swift, setSwift] = useState("");
  const [documentNumber, setDocumentNumber] = useState("");
  const { showSnackBar } = useFeedback();
  const [editing, setEditing] = useState(false);
  const callGetBankData = async () => {
    await callEndpoint(getAccountData(userState.token), successGetDataCallback);
  };
  const successGetDataCallback = (data: any) => {
    setBankNumber(data.bankNumber);
    setBankName(data.bankName);
    setHolder(data.holder);
    setDocumentType(data.documentType);
    setIban(data.IBAN);
    setSwift(data.SWIFT);
    setDocumentNumber(data.documentNumber);
    if (data.bankNumber != "") {
      setEditing(false);
    }
  };
  const successCallback = (data: any) => {
    showSnackBar("success", "Bank Account Updated");
    callGetBankData();
  };
  const handleEditClick = () => {
    setEditing(true);
  };

  useEffect(() => {
    callGetBankData();
  }, []);

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const formData = new FormData();
    formData.append("bankNumber", bankNumber);
    formData.append("bankName", bankName);
    formData.append("holder", holder);
    formData.append("documentType", documentType);
    formData.append("IBAN", iban);
    formData.append("SWIFT", swift);
    formData.append("documentNumber", documentNumber);
    const formattedData = JSON.stringify(Object.fromEntries(formData));
    await callEndpoint(saveAccountData(formattedData, userState.token), successCallback);
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setDocumentType(event.target.value);
  };

  return (
    <>
      <form autoComplete="off" noValidate {...props} onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item lg={8} md={6} xs={12}>
            {editing ? (
              <Card>
                <CardHeader subheader="This account will be used to transfer the money coming from your supporters." title="Bank Account" />
                <Divider />
                <CardContent>
                  <Grid container spacing={3}>
                    <Grid item md={12} xs={12}>
                      <TextField
                        fullWidth
                        label="Holder Name"
                        name="holder"
                        required
                        value={holder}
                        variant="outlined"
                        onChange={(e) => setHolder(e.target.value)}
                      />
                    </Grid>
                    <Grid item md={12} xs={12}>
                      <TextField
                        id="outlined-select-currency"
                        select
                        label="Select"
                        value={documentType}
                        onChange={handleChange}
                        helperText="Please select your document type"
                      >
                        <MenuItem value={"DNI"}>DNI</MenuItem>
                        <MenuItem value={"Passport"}>Passport</MenuItem>
                        <MenuItem value={"Driver License"}>Driver License</MenuItem>
                      </TextField>
                    </Grid>
                    <Grid item md={12} xs={12}>
                      <TextField
                        fullWidth
                        label="Document Number"
                        name="documentNumber"
                        required
                        value={documentNumber}
                        variant="outlined"
                        onChange={(e) => setDocumentNumber(e.target.value)}
                      />
                    </Grid>
                    <Grid item md={12} xs={12}>
                      <TextField
                        fullWidth
                        label="Bank Name"
                        name="bankName"
                        required
                        value={bankName}
                        variant="outlined"
                        onChange={(e) => setBankName(e.target.value)}
                      />
                    </Grid>
                    <Grid item md={12} xs={12}>
                      <TextField
                        fullWidth
                        label="Bank Number"
                        name="bankNumber"
                        required
                        value={bankNumber}
                        variant="outlined"
                        onChange={(e) => setBankNumber(e.target.value)}
                      />
                    </Grid>
                    <Grid item md={12} xs={12}>
                      <TextField
                        fullWidth
                        label="IBAN"
                        name="iban"
                        value={iban}
                        variant="outlined"
                        onChange={(e) => setIban(e.target.value)}
                      />
                    </Grid>
                    <Grid item md={12} xs={12}>
                      <TextField
                        fullWidth
                        label="SWIFT"
                        name="swift"
                        value={swift}
                        variant="outlined"
                        onChange={(e) => setSwift(e.target.value)}
                      />
                    </Grid>
                  </Grid>
                </CardContent>
                <Divider />
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "flex-end",
                    p: 2,
                  }}
                >
                  <Button type="submit" sx={{ marginRight: 2 }} color="primary" variant="outlined">
                    {loading ? <span>Updating...</span> : <span>Update</span>}
                  </Button>
                </Box>
              </Card>
            ) : (
              <Box>
                <Card
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    height: "100%",
                    marginBottom: "50px",
                  }}
                >
                  <CardHeader title={"Holder: " + holder}></CardHeader>
                  <Divider></Divider>
                  <CardContent>
                    <Grid container direction="row" spacing={1}>
                      <Grid item xs={4}>
                        <Typography sx={{ fontWeight: "bold" }}>
                          Document Type: <Typography>{documentType}</Typography>
                        </Typography>
                      </Grid>
                      <Grid item xs={4}>
                        <Typography sx={{ fontWeight: "bold" }}>
                          Document Number: <Typography>{documentNumber}</Typography>
                        </Typography>
                      </Grid>
                      <Grid item xs={4}>
                        <Typography sx={{ fontWeight: "bold" }}>
                          Bank Name: <Typography>{bankName}</Typography>
                        </Typography>
                      </Grid>
                      <Grid item xs={4}>
                        <Typography sx={{ fontWeight: "bold" }}>
                          Bank Number: <Typography>{bankNumber}</Typography>
                        </Typography>
                      </Grid>
                      <Grid item xs={4}>
                        <Typography sx={{ fontWeight: "bold" }}>
                          SWIFT: <Typography>{swift}</Typography>
                        </Typography>
                      </Grid>
                      <Grid item xs={4}>
                        <Typography sx={{ fontWeight: "bold" }}>
                          IBAN: <Typography>{iban}</Typography>
                        </Typography>
                      </Grid>
                    </Grid>
                  </CardContent>
                  <Divider></Divider>
                  <CardActions>
                    <IconButton aria-label="add to favorites" color="primary" sx={{ marginLeft: "auto" }} onClick={handleEditClick}>
                      <EditIcon />
                    </IconButton>
                  </CardActions>
                </Card>
              </Box>
            )}
          </Grid>
        </Grid>
      </form>
    </>
  );
};
