import { Fragment, useEffect, useRef, useState } from "react";
import { Avatar, Box, Button, Card, CardContent, CardHeader, Divider, Grid, TextField, CardActions, Typography } from "@mui/material";
import { ProfileInterface, SocialMediaInterface, UserInterface } from "../../models/interfaces";
import { AppStore } from "../../redux/store";
import { useDispatch, useSelector } from "react-redux";
import { useFetchAndLoad } from "../../hooks";
import { createProfile, getProfile } from "../../services/dashboard.service";
import { newProfile } from "../../redux/states/profile";
import { API_BASE_URL } from "../../utilities";
import { useNavigate } from "react-router-dom";
import useFeedback from "../../hooks/useFeedback";

export const AccountProfileForm = (props: any) => {
  const navigate = useNavigate();
  const userState: UserInterface = useSelector((store: AppStore) => store.user);
  const dispatch = useDispatch();
  const { loading, callEndpoint } = useFetchAndLoad();
  const { showSnackBar } = useFeedback();
  const emptySocialLink: SocialMediaInterface = {
    name: "",
    link: "",
  };
  const [profileImgUrl, setProfileImgUrl] = useState("");
  const [profileImgData, setProfileImgData] = useState("");
  const [socialLinks, setSocialLinks] = useState([emptySocialLink]);
  const [description, setDescription] = useState("");
  const [location, setLocation] = useState("");
  const [profession, setProfession] = useState("");
  const [token_benefits, setBenefits] = useState("");
  const [events_held, setEvents] = useState("");

  const hiddenFileInput = useRef<HTMLInputElement>(null);

  useEffect(() => {
    getUserProfile();
  }, []);

  const goToProfile = () => {
    window.open("/profile/creator/" + userState.user.username, "_self");
  };

  const getProfileSuccessCallback = (data: any) => {
    const profileData: ProfileInterface = data;
    if (profileData) {
      dispatch(newProfile(profileData));
      setProfileImgUrl(API_BASE_URL + "public/uploads/profiles/" + profileData.image);
      //console.log(API_BASE_URL+"public/uploads/profiles/"+profileData.image);
      const objCopy = { ...profileData.social_media.toString };
      setSocialLinks(JSON.parse(JSON.stringify(profileData.social_media)));
      setDescription(profileData.description);
      setLocation(profileData.location);
      setProfession(profileData.profession);
      setBenefits(profileData.token_benefits);
      setEvents(profileData.events_held);
    }
  };

  const getUserProfile = async () => {
    await callEndpoint(getProfile(userState.token), getProfileSuccessCallback);
  };

  const handleUploadImageButton = () => {
    if (hiddenFileInput.current) {
      hiddenFileInput.current.click();
    }
  };

  const handleImageChange = (event: any) => {
    const imageFile = event.target.files;
    setProfileImgData(imageFile[0]);
    setProfileImgUrl(URL.createObjectURL(imageFile[0]));
  };

  const handleChangeDescription = (event: any) => {
    setDescription(event.target.value);
  };

  const handleChangeSocialLinks = (i: number, e: any) => {
    let socialValues = [...socialLinks];
    let fieldName = String(e.target.name);
    socialValues[i][fieldName as keyof typeof emptySocialLink] = e.target.value;
    setSocialLinks(socialValues);
  };

  const addNewSocialLink = () => {
    setSocialLinks((socialLinks) => [...socialLinks, emptySocialLink]);
  };

  const createProfileSuccessCallback = (data: any) => {
    const profileData: ProfileInterface = data;
    if (profileData) {
      dispatch(newProfile(profileData));
      showSnackBar("success", "Profile saved successfully");
    }
  };

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const formData = new FormData();
    formData.append("description", description);
    formData.append("profession", profession);
    formData.append("location", location);
    formData.append("events_held", events_held);
    formData.append("token_benefits", token_benefits);
    formData.append("social_media", JSON.stringify(socialLinks));
    formData.append("file", profileImgData);
    await callEndpoint(createProfile(formData, userState.token), createProfileSuccessCallback);
  };

  const removeSocialLinks = (i: number) => {
    let socialValues = [...socialLinks];
    socialValues.splice(i, 1);
    setSocialLinks(socialValues);
  };
  const handleChangeLocation = (event: any) => {
    setLocation(event.target.value);
  };
  const handleChangeProfession = (event: any) => {
    setProfession(event.target.value);
  };
  const handleChangeBenefits = (event: any) => {
    setBenefits(event.target.value);
  };
  const handleChangeEvents = (event: any) => {
    setEvents(event.target.value);
  };

  return (
    <form autoComplete="off" noValidate {...props} onSubmit={handleSubmit}>
      <Grid container spacing={3}>
        <Grid item lg={4} md={6} xs={12}>
          <Card {...props}>
            <CardContent>
              <Box
                sx={{
                  alignItems: "center",
                  display: "flex",
                  flexDirection: "column",
                }}
              >
                <Avatar
                  src={profileImgUrl}
                  sx={{
                    height: 200,
                    mb: 2,
                    width: 200,
                  }}
                />
                <Typography color="textPrimary" gutterBottom variant="h5">
                  {userState.user.name}
                </Typography>
              </Box>
            </CardContent>
            <Divider />
            <CardActions>
              <Button color="primary" fullWidth variant="text" onClick={handleUploadImageButton}>
                <input
                  accept=".png, .jpg, .jpeg, .webp"
                  id="contained-button-file"
                  name="file"
                  type="file"
                  style={{ display: "none" }}
                  ref={hiddenFileInput}
                  onChange={handleImageChange}
                />
                Upload profile picture
              </Button>
            </CardActions>
          </Card>
        </Grid>
        <Grid item lg={8} md={6} xs={12} sx={{ mb: 10 }}>
          <Card>
            <CardHeader subheader="The information can be edited" title="Profile" />
            <Divider />
            <CardContent>
              <Grid container spacing={3}>
                <Grid item md={12} xs={12}>
                  <TextField
                    fullWidth
                    helperText="Please describe yourself and what you offer to your community."
                    label="Description"
                    name="description"
                    rows={6}
                    onChange={handleChangeDescription}
                    required
                    multiline
                    value={description}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={12} xs={12}>
                  <TextField
                    fullWidth
                    helperText="Your location"
                    label="Location"
                    name="location"
                    onChange={handleChangeLocation}
                    required
                    value={location}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={12} xs={12}>
                  <TextField
                    fullWidth
                    helperText="Your profession"
                    label="Profession"
                    name="profession"
                    onChange={handleChangeProfession}
                    required
                    value={profession}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={12} xs={12}>
                  <TextField
                    fullWidth
                    helperText="Please describe your tokens benefits."
                    label="Token Benefits"
                    name="benefits"
                    rows={6}
                    onChange={handleChangeBenefits}
                    required
                    multiline
                    value={token_benefits}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={12} xs={12}>
                  <TextField
                    fullWidth
                    helperText="Your profession"
                    label="Events Held"
                    name="events"
                    onChange={handleChangeEvents}
                    required
                    value={events_held}
                    variant="outlined"
                  />
                </Grid>
                {socialLinks.map((item, index) => (
                  <Fragment key={index}>
                    <Grid item md={5} xs={12} key={index + "socialname"}>
                      <TextField
                        fullWidth
                        label="Social Media Name"
                        name="name"
                        onChange={(e) => handleChangeSocialLinks(index, e)}
                        required
                        value={item.name}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid item md={5} xs={12} key={index + "sociallink"}>
                      <TextField
                        fullWidth
                        label="Social Media Link"
                        name="link"
                        onChange={(e) => handleChangeSocialLinks(index, e)}
                        required
                        value={item.link}
                        variant="outlined"
                      />
                    </Grid>
                    <Grid item md={2} xs={12} key={index + "socialremove"}>
                      <Button onClick={() => removeSocialLinks(index)}>Remove</Button>
                    </Grid>
                  </Fragment>
                ))}
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "flex-end",
                    p: 2,
                  }}
                >
                  <Button color="primary" variant="contained" onClick={addNewSocialLink}>
                    Add other social media
                  </Button>
                </Box>
              </Grid>
            </CardContent>
            <Divider />
            <Box
              sx={{
                display: "flex",
                justifyContent: "flex-end",
                p: 2,
              }}
            >
              <Button sx={{ marginRight: 2 }} color="primary" variant="outlined" onClick={goToProfile}>
                View profile
              </Button>
              <Button type="submit" color="primary" variant="contained">
                {loading ? <span>Updating...</span> : <span>Update profile</span>}
              </Button>
            </Box>
          </Card>
        </Grid>
      </Grid>
    </form>
  );
};
