import * as React from "react";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import { useDispatch } from "react-redux";
import { useFetchAndLoad } from "../../hooks";
import { createUser } from "../../redux/states/user";
import { register } from "../../services/login.service";
import { useNavigate } from "react-router-dom";
import { registerFormStructure, UserInterface } from "../../models/interfaces";
import { useState } from "react";
import { emailValidation } from "../../utilities/validate-email-field";

export const RegisterForm = () => {
  const { loading, callEndpoint } = useFetchAndLoad();
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const initialValues: registerFormStructure = { name: "", email: "", password: "" };

  const [formValues, setFormValues] = useState<registerFormStructure>(initialValues);
  const [formErrors, setFormErrors] = useState<registerFormStructure>(initialValues);

  const handleInputChange = (e: any) => {
    const { name, value } = e.target;
    const newFormValues: registerFormStructure = {
      ...formValues,
      [name]: value,
    };
    setFormValues(newFormValues);
  };

  const validate = () => {
    let temp: registerFormStructure = initialValues;
    temp.name = formValues.name.length > 0 ? "" : "Name cannot be empty";
    temp.email = emailValidation(formValues.email) ? "" : "Email is not valid";
    temp.password = formValues.password.length > 0 ? "" : "Password cannot be empty";
    setFormErrors({ ...temp });
    return Object.values(temp).every((x) => x == "");
  };

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (validate()) {
      const formData = new FormData(event.currentTarget);
      const formattedData = JSON.stringify(Object.fromEntries(formData));
      await callEndpoint(register(formattedData), successCallback);
    }
  };

  const successCallback = (data: any) => {
    const userData: UserInterface = data;
    sessionStorage.setItem("authtoken", userData.token);
    sessionStorage.setItem("username", userData.user.username);
    dispatch(createUser(userData));
    navigate("/creator-profile");
  };

  return (
    <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <TextField
            error={formErrors.name != ""}
            helperText={formErrors.name}
            onChange={(e) => handleInputChange(e)}
            value={formValues.name}
            autoComplete="given-name"
            name="name"
            required
            fullWidth
            id="name"
            label="Name"
            autoFocus
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            error={formErrors.email != ""}
            helperText={formErrors.email}
            onChange={(e) => handleInputChange(e)}
            value={formValues.email}
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
          />
        </Grid>

        <Grid item xs={12}>
          <TextField
            error={formErrors.password != ""}
            helperText={formErrors.password}
            onChange={(e) => handleInputChange(e)}
            value={formValues.password}
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
          />
          <input name="type" defaultValue="1" hidden></input>
        </Grid>
      </Grid>
      <Button type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
        {loading ? <span>loading</span> : <span>Sign Up</span>}
      </Button>
      <Grid container justifyContent="flex-end">
        <Grid item>
          <Link href="/login" variant="body2">
            Already have an account? Sign in
          </Link>
        </Grid>
      </Grid>
    </Box>
  );
};
