import React from "react";
import ReactDOM from "react-dom/client";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { Login, Register } from "./pages/Login/";
import { Provider } from "react-redux";
import store from "./redux/store";
import ProtectedRoutes from "./components/ProtectedRoutes";
import { theme } from "./theme";
import { ThemeProvider } from "@mui/material/styles";
import "./App.scss";
import CreatorProfile from "./pages/Dashboard/CreatorProfile";
import PublicProfile from "./pages/Profile/PublicProfile";
import CreatorTokens from "./pages/Dashboard/CreatorTokens";
import CreateNewToken from "./pages/Dashboard/CreateNewToken";
import { ImportToken } from "./pages/Dashboard";
import { PopupMessage } from "./components/feedback/popup-message";
import { CreatorPayment } from "./pages/Payment";
import { SnackBarMessage } from "./components/feedback/snackbar-message";
import GlobalStylesStyled from "./styled-components/GlobalStyles.styled";

ReactDOM.createRoot(document.getElementById("root")!).render(
  <Provider store={store}>
    <ThemeProvider theme={theme}>
      <PopupMessage></PopupMessage>
      <SnackBarMessage></SnackBarMessage>
      <GlobalStylesStyled></GlobalStylesStyled>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Login />} />
          <Route path="login" element={<Login />} />
          <Route path="register" element={<Register />} />
          <Route path="profile/creator/:userName" element={<PublicProfile />} />

          <Route element={<ProtectedRoutes />}>
            <Route path="creator-profile" element={<CreatorProfile />} />
            <Route path="creator-payment" element={<CreatorPayment />} />
            <Route path="creator-tokens" element={<CreatorTokens />} />
            <Route path="create-new-token" element={<CreateNewToken />} />
            <Route path="import-token" element={<ImportToken />} />
          </Route>
        </Routes>
      </BrowserRouter>
    </ThemeProvider>
  </Provider>
);
