import { Box, Button, Typography } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import Popup from "reactjs-popup";
import useAddToken from "../../hooks/useAddToken";
import useFeedback from "../../hooks/useFeedback";
import { PopupMessageInterface } from "../../models/interfaces/Popup.interface";
import { modifyPopup } from "../../redux/states/popup";
import { AppStore } from "../../redux/store";

export const PopupMessage = () => {
  const popupState: PopupMessageInterface = useSelector((store: AppStore) => store.popup);
  const dispatcher = useDispatch();
  const { addToken } = useAddToken();
  const { showSnackBar} = useFeedback();
  const closePopup = () => {
    dispatcher(modifyPopup({ openned: false }));
  };

  const handleAddTokenButton = () => {
    addToken(popupState.tokenAddress, successCallback);
  };

  const successCallback = () => {
    showSnackBar("success", "Token added correctly");
  };

  return (
    <div>
      <Popup open={popupState.openned} closeOnDocumentClick onClose={closePopup}>
        <div className="modal">
          <a className="close" onClick={closePopup}>
            &times;
          </a>
          <div className="header">
            <Typography variant="subtitle1">{popupState.title}</Typography>
          </div>
          <div className="content">
            <Typography variant="body1">{popupState.content}</Typography>
          </div>
          {popupState.tokenAddress ? (
            <Box textAlign="center">
              <Button size="large" variant="contained" className="add-button" onClick={handleAddTokenButton}>
                Add token
              </Button>
            </Box>
          ) : (
            ""
          )}
        </div>
      </Popup>
    </div>
  );
};
