import { createSlice } from "@reduxjs/toolkit";
import { PopupMessageInterface } from "../../models/interfaces/Popup.interface";

export const PopupEmptyState: PopupMessageInterface = {
  openned: false,
  title: "",
  content: "",
  tokenName: "",
  tokenAddress: "",
};

export const popupSlice = createSlice({
  name: "popup",
  initialState: PopupEmptyState,
  reducers: {
    modifyPopup: (state, action) => ({ ...state, ...action.payload }),
    resetPopup: (state, action) => PopupEmptyState,
  },
});

export const { modifyPopup, resetPopup } = popupSlice.actions;

export default popupSlice.reducer;
