import axios from "axios";
import { API_BASE_URL, loadAbort } from "./../utilities";

const baseUrl: string = API_BASE_URL;

export const login = (data: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
  };
  return {
    call: axios.post(baseUrl + "auth/login", data, { headers: headers, signal: controller.signal }),
    controller,
  };
};

export const register = (data: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
  };
  return {
    call: axios.post(baseUrl + "auth/register", data, { headers: headers, signal: controller.signal }),
    controller,
  };
};

export const updateWallet = (data: string, authToken: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
    Authorization: "Bearer " + authToken,
  };
  return {
    call: axios.post(baseUrl + "auth/setUserWallet", data, { headers: headers, signal: controller.signal }),
    controller,
  };
};

export const getUser = (authToken: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
    Authorization: "Bearer " + authToken,
  };
  return {
    call: axios.get(baseUrl + "auth/user", { headers: headers, signal: controller.signal }),
    controller,
  };
};
