import { Button, TextField } from "@mui/material";
import { useDispatch } from "react-redux";
import { useFetchAndLoad } from "../../hooks";
import { createUser } from "../../redux/states/user";
import { login } from "../../services/login.service";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import { useNavigate } from "react-router";
import { loginFormStructure, UserInterface } from "../../models/interfaces";
import { useState } from "react";
import { CssOutlined } from "@mui/icons-material";
import { emailValidation } from "../../utilities/validate-email-field";

export const LoginForm = () => {
  const { loading, callEndpoint } = useFetchAndLoad();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [rememberMe, setRememberMe] = useState(false);


  const initialValues: loginFormStructure = { email: "", password: "" };

  const [formValues, setFormValues] = useState<loginFormStructure>(initialValues);
  const [formErrors, setFormErrors] = useState<loginFormStructure>(initialValues);

  const handleInputChange = (e: any) => {
    const { name, value } = e.target;
    const newFormValues: loginFormStructure = {
      ...formValues,
      [name]: value,
    };
    setFormValues(newFormValues);
  };

  const validate = () => {
    let temp: loginFormStructure = initialValues;
    temp.email = emailValidation(formValues.email) ? "" : "Email is not valid";
    temp.password = formValues.password.length > 0 ? "" : "Password cannot be empty";
    setFormErrors({ ...temp });
    return Object.values(temp).every((x) => x == "");
  };

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    if (validate()) {
      const formData = new FormData(event.currentTarget);
      const formattedData = JSON.stringify(Object.fromEntries(formData));
      await callEndpoint(login(formattedData), successCallback);
    }
  };

  const successCallback = (data: any) => {
    const userData: UserInterface = data;

    if (rememberMe) {
      localStorage.setItem("authtoken", userData.token);
      localStorage.setItem("username", userData.user.username);
    } else {
      localStorage.setItem("authtoken", "");
      localStorage.setItem("username", "");
      sessionStorage.setItem("authtoken", userData.token);
      sessionStorage.setItem("username", userData.user.username);
    }
    dispatch(createUser(userData));
    //navigate("/creator-profile");
    window.location.href = "/creator-profile";
  };

  return (
    <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 1 }}>
      <TextField
        error={formErrors.email != ""}
        helperText={formErrors.email}
        margin="normal"
        required
        fullWidth
        id="email"
        label="Email Address"
        name="email"
        onChange={(e) => handleInputChange(e)}
        value={formValues.email}
        autoFocus
      />
      <TextField
        error={formErrors.password != ""}
        helperText={formErrors.password}
        margin="normal"
        required
        fullWidth
        name="password"
        label="Password"
        type="password"
        onChange={(e) => handleInputChange(e)}
        value={formValues.password}
        id="password"
      />
      <FormControlLabel
        control={
          <Checkbox
            onChange={(e) => {
              setRememberMe(e.target.checked);
              console.log(e.target.checked);
            }}
            checked={rememberMe}
          />
        }
        label="Remember me"
      />
      <Button type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
        {loading ? <span>loading</span> : <span>Sign In</span>}
      </Button>
      <Grid container>
        <Grid item>
          <Link href="/register" variant="body2">
            {"Don't have an account? Sign Up"}
          </Link>
        </Grid>
      </Grid>
    </Box>
  );
};
