import { useEffect, useState } from "react";
import Web3 from "web3";
import WalletConnect from "@walletconnect/client";
import QRCodeModal from "@walletconnect/qrcode-modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import useFeedback from "./useFeedback";

const walletConnectProvider = new WalletConnectProvider({
  rpc: {
    97: 'https://data-seed-prebsc-1-s1.binance.org:8545/',
  },
});

const useWalletConnection = () => {
  const [connectedWallet, setConnectedWallet] = useState("");
  const [connectButtonText, setConnectButtonText] = useState("Connect Wallet");
  const [web3Provider, setWeb3Provider] = useState<Web3>();
  const { showPopup } = useFeedback();
  // BSC TESTNET: 0x61
  // BSC MAINNET: 0x38
  const bscChainId = "0x61";
  //await provider.enable();
  useEffect(() => {
    if (window.ethereum && window.ethereum.isMetaMask) {
      window.ethereum.on("accountsChanged", accountChangeHandler);
      window.ethereum.on("chainChanged", chainChangedHandler);
      console.log("listeners created");
    }
  }, []);

  // Create a connector
  const connector = new WalletConnect({
    bridge: "https://bridge.walletconnect.org", // Required
    qrcodeModal: QRCodeModal,
  });

  /*if (!connector.connected) {
    // create new session
    connector.createSession();
  }*/


  // Check if connection is already established
  const connectWithWalletConnect = () => {
    if (!connector.connected) {
      // create new session
      connector.createSession();
    }
  };

  // Subscribe to connection events
  connector.on("connect", (error, payload) => {
    if (error) {
      throw error;
    }

    const { accounts, chainId } = payload.params[0];

    updateProvider(walletConnectProvider);
    setConnectedWallet(accounts[0]);
    setConnectButtonText("Connected");
  });

  connector.on("session_update", (error, payload) => {
    if (error) {
      throw error;
    }
    const { accounts, chainId } = payload.params[0];

    //await provider.enable();
    updateProvider(walletConnectProvider);
    setConnectedWallet(accounts[0]);
    setConnectButtonText("Connected");
    // Get updated accounts and chainId
  });

  connector.on("disconnect", (error, payload) => {
    if (error) {
      throw error;
    }
    setConnectedWallet("");
    setConnectButtonText("Connect Wallet");
    setWeb3Provider(undefined);
    // Delete connector
  });

  const connectWallet = () => {
    if (connectedWallet == "") {
      if (window.ethereum && window.ethereum.isMetaMask) {
        if (window.ethereum.chainId == bscChainId) {
          window.ethereum.request({ method: "eth_requestAccounts" }).then((result: string[]) => {
            updateProvider(window.ethereum);
            setConnectedWallet(result[0]);
            setConnectButtonText("Connected");
          });
        } else {
          showPopup("Something happened", "Change the network, only BSC is allowed");
        }
      } else {
        showPopup("Something happened", "Something Happened while connecting, try again");
      }
    }
  };

  const updateProvider = async (provider: any) => {
    const tempProvider = new Web3(provider);
    setWeb3Provider(tempProvider);
  };

  const accountChangeHandler = () => {
    setConnectedWallet("");
    setConnectButtonText("Connect Wallet");
    setWeb3Provider(undefined);
  };
  const chainChangedHandler = () => {
    setConnectedWallet("");
    setConnectButtonText("Connect Wallet");
    setWeb3Provider(undefined);
  };

  return { connectWallet, connectedWallet, connectButtonText, web3Provider, connectWithWalletConnect };
};

export default useWalletConnection;
