import { useDispatch } from "react-redux";
import { SnackBarInterface } from "../models/interfaces";
import { modifyPopup, resetPopup } from "../redux/states/popup";
import { modifySnackBar, resetSnackBar } from "../redux/states/snackbar";

const useFeedback = () => {
  const dispatcher = useDispatch();
  const showPopup = (title: string, message: string) => {
    const popupObject = {
      openned: true,
      title: title,
      content: message,
    };
    dispatcher(modifyPopup(popupObject));
  };

  const showPopupWithToken = (title: string, message: string, tokenAddress: string) => {
    const popupObject = {
      openned: true,
      title: title,
      content: message,
      tokenAddress: tokenAddress,
    };
    dispatcher(modifyPopup(popupObject));
  };

  const closePopup = () => {
    dispatcher(resetPopup);
  };

  const showSnackBar = (type: string, message: string) => {
    const snackBarObject: SnackBarInterface = {
      type: type,
      message: message,
      opened: true,
    };
    dispatcher(modifySnackBar(snackBarObject));
  };

  const closeSnackBar = () => {
    dispatcher(resetSnackBar);
  };

  return { showPopup, showPopupWithToken, closePopup, showSnackBar, closeSnackBar };
};

export default useFeedback;
