import { Box, Grid } from "@mui/material";
import { TokenCard } from "../../components/tokens/";
import { TokenInterface, UserInterface } from "../../models/interfaces";
import { useFetchAndLoad } from "../../hooks";
import { getUserTokens } from "../../services";
import { useSelector } from "react-redux";
import { AppStore } from "../../redux/store";
import { useEffect, useState } from "react";

export const TokenList = () => {
  const { loading, callEndpoint } = useFetchAndLoad();
  const userState: UserInterface = useSelector((store: AppStore) => store.user);
  const emptyTokenArray: TokenInterface[] = [];
  const [tokens, setTokens] = useState(emptyTokenArray);

  useEffect(() => {
    getTokenData();
  }, []);

  const getTokenSuccessCallback = (data: any) => {
    const tokenData: TokenInterface[] = data;
    if (tokenData) {
      setTokens(JSON.parse(JSON.stringify(tokenData)));
    }
  };

  const getTokenData = async () => {
    await callEndpoint(getUserTokens(userState.token), getTokenSuccessCallback);
  };

  return (
    <Box sx={{ pt: 3 }}>
      <Grid container spacing={3}>
        {tokens.map((token) => (
          <Grid item key={token.symbol} lg={4} md={6} xs={12}>
            <TokenCard name={token.name} symbol={token.symbol} address={token.address} description={token.description} id={token.id} imported={token.imported} default={token.default} />
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};
