import Web3 from "web3";
import tokenData from "../token/UpSporeToken.json";
import { UPSPORE_WALLET } from "../utilities/get-contract-data";
import useFeedback from "./useFeedback";

const useRequestAllowance = () => {
  const UpSporeWallet = UPSPORE_WALLET;
  const contractABI: string = JSON.stringify(tokenData.abi);
  const { showPopup, closePopup } = useFeedback();

  const requestAllowance = async (web3: Web3, creator: string, tokenAddress: string) => {
    try {
      const contract = new web3.eth.Contract(JSON.parse(contractABI), tokenAddress);

      const creatorBalance = await contract.methods.balanceOf(creator).call();
      const approveTx = await contract.methods.approve(UpSporeWallet, creatorBalance).send({ from: creator });
      return approveTx;

    } catch (e: any) {
        showPopup("Oh no!", "There was an error approving the allowance.");
      return false;
    }
  };

  return { requestAllowance };
};

export default useRequestAllowance;
