import axios from "axios";
import { API_BASE_URL, loadAbort } from "./../utilities";

const baseUrl: string = API_BASE_URL;

export const createProfile = (data: FormData, authToken: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "multipart/form-data",
    Authorization: "Bearer " + authToken,
  };
  return {
    call: axios.post(baseUrl + "profile", data, { headers: headers, signal: controller.signal }),
    controller,
  };
};

export const getProfile = (authToken: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
    Authorization: "Bearer " + authToken,
  };
  return {
    call: axios.get(baseUrl + "profile", { headers: headers, signal: controller.signal }),
    controller,
  };
};

export const getFullProfile = (profileUserName: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
  };
  return {
    call: axios.get(baseUrl + "profile/creator/" + profileUserName, { headers: headers, signal: controller.signal }),
    controller,
  };
};
