import Web3 from "web3";
import tokenData from "../token/UpSporeToken.json";
import { AbiItem } from "web3-utils";

const useAddToken = () => {
  const addToken = async (tokenAddress: string, successCallback: () => void) => {
    try {
      const web3Provider = new Web3(window.ethereum);
      const contract = new web3Provider.eth.Contract(tokenData.abi as AbiItem[], tokenAddress);

      const tokenSymbol = await contract.methods.symbol().call();

      const wasAdded = await window.ethereum.request({
        method: "wallet_watchAsset",
        params: {
          type: "ERC20", // Initially only supports ERC20, but eventually more!
          options: {
            address: tokenAddress, // The address that the token is at.
            symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
            decimals: 18, // The number of decimals in the token
          },
        },
      });

      if (wasAdded) {
        successCallback();
      } else {
        console.log("Request Rejected");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return { addToken };
};

export default useAddToken;
