import Web3 from "web3";
import tokenData from "../token/UpSporeToken.json";
import { INITIAL_SUPPLY, UPSPORE_WALLET } from "../utilities/get-contract-data";
import useFeedback from "./useFeedback";
import { AbiItem } from "web3-utils";

const useDeployToken = () => {
  const UpSporeWallet = UPSPORE_WALLET;
  const initialsupply = INITIAL_SUPPLY;
  const contractByteCode: string = tokenData.bytecode;

  const { showPopup, closePopup } = useFeedback();
  const deployToken = async (web3: Web3, creator: string, name: string, symbol: string) => {
    try {
      const options = {
        data: contractByteCode,
        arguments: [creator, UpSporeWallet, initialsupply, name, symbol],
      };
      const tx = {
        from: creator,
        gas: 15000000,
        gasPrice: "10000000000",
      };

      console.log(tokenData.abi);

      const contract = new web3.eth.Contract(tokenData.abi as AbiItem[]);
      console.log(contract);
      let resultingContract = "";
      await contract
        .deploy(options)
        .send(tx, function (error, transactionHash) {})
        .on("error", function (error) {
          showPopup("On no!", "There was an error deploying the contract");
        })
        .on("transactionHash", function (transactionHash) {
          console.log("TxHash: " + transactionHash);
          showPopup("Awesome!", "We are deploying your token contract");
        })
        .then(function (newContractInstance) {
          closePopup();
          console.log("ContractAddress: " + newContractInstance.options.address);
          resultingContract = newContractInstance.options.address;
          return resultingContract; // instance with the new contract address
        });

      return resultingContract;
    } catch (e: any) {
      console.log(e);
      return "";
    }
  };

  return { deployToken };
};

export default useDeployToken;
