import { Box, Button, Container } from "@mui/material";
import { DashboardLayout } from "../../components/dashboard/dashboard-layout";
import { useNavigate } from "react-router-dom";
import { TokenList } from "../../components/tokens";

export default function CreatorTokens() {
  const navigation = useNavigate();

  const goToCreateTokenForm = () => {
    navigation("/create-new-token");
  };
  const goToImportTokenForm = () => {
    navigation("/import-token");
  };
  return (
    <>
      <DashboardLayout>
        <Box
          component="main"
          sx={{
            flexGrow: 1,
          }}
        >
          <Container maxWidth={false}>
            <Box
              sx={{
                alignItems: "right",
                display: "flex",
                flexWrap: "wrap",
                m: -1,
              }}
            >
              <Button color="primary" variant="contained" onClick={goToCreateTokenForm}>
                Create Token
              </Button>
              <Button sx={{ marginLeft: 2 }} color="primary" variant="outlined" onClick={goToImportTokenForm}>
                Import Token
              </Button>
            </Box>
            <TokenList></TokenList>
            <Box
              sx={{
                display: "flex",
                justifyContent: "center",
                pt: 3,
              }}
            ></Box>
          </Container>
        </Box>
      </DashboardLayout>
    </>
  );
}
