import { configureStore } from "@reduxjs/toolkit";
import { ProfileInterface, SnackBarInterface, UserInterface } from "../models/interfaces";
import { PopupMessageInterface } from "../models/interfaces/Popup.interface";
import { popupSlice } from "./states/popup";
import { profileSlice } from "./states/profile";
import { snackbarSlice } from "./states/snackbar";
import { userSlice } from "./states/user";

export interface AppStore {
  user: UserInterface;
  profile: ProfileInterface;
  popup: PopupMessageInterface;
  snackbar: SnackBarInterface;
}

export default configureStore<AppStore>({
  reducer: {
    user: userSlice.reducer,
    profile: profileSlice.reducer,
    popup: popupSlice.reducer,
    snackbar: snackbarSlice.reducer,
  },
});
