import { useDispatch, useSelector } from "react-redux";
import { useFetchAndLoad } from "../../hooks";

import { AppStore } from "../../redux/store";
import { useNavigate, useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { getFullProfile } from "../../services/dashboard.service";
import { ProfileInterface, UserInfoInterface, UserInterface } from "../../models/interfaces";
import { newProfile } from "../../redux/states/profile";
import { API_BASE_URL } from "../../utilities";
import twitchIcon from "../../assets/twitch_icon.png";
import { modifyUser } from "../../redux/states/user";
import { UpParagraph } from "../../styled-components/UpParagraph.style";
import { UpGap } from "../../styled-components/UpGap.styled";
import {
  UpAboutSubtitle,
  UpButton,
  UpContainer,
  UpContainerWidth,
  UpFullWidthContainer,
  UpProfileImg,
  UpRow,
  UpSocialsContainer,
} from "../../styled-components";
import { UpMainTitle } from "../../components/template-components/UpMainTitle";
import { UpTitle } from "../../styled-components/UpTitle.styled";
import { UpNumberAbout } from "../../styled-components/UpNumberAbout.styled";
import { Donate } from "../Payment";

export default function PublicProfile() {
  const [openSupport, setOpenSupport] = useState(false);
  const { loading, callEndpoint } = useFetchAndLoad();
  const dispatch = useDispatch();
  const userState: UserInterface = useSelector((store: AppStore) => store.user);
  console.log(userState);
  const profileState: ProfileInterface = useSelector((store: AppStore) => store.profile);
  const navigation = useNavigate();
  const { userName = "" } = useParams();
  const [creatorTokenName, setCreatorTokenName] = useState("");
  useEffect(() => {
    getUserFullProfile();
  }, []);

  const getProfileSuccessCallback = (data: any) => {
    const profileData: ProfileInterface = data.profile;
    const userData: UserInfoInterface = data.user;
    const tokensData = data.tokens;

    for (let i = 0; i < tokensData.length; i++) {
      if (tokensData[i].default) {
        setCreatorTokenName(tokensData[i].symbol);
      }
    }
    console.log(data);
    if (profileData && userData) {
      dispatch(newProfile(profileData));
      dispatch(modifyUser({ user: userData }));
    }
  };
  const getUserFullProfile = async () => {
    await callEndpoint(getFullProfile(userName), getProfileSuccessCallback);
  };

  const goToDonationPage = () => {
    setOpenSupport(true);
  };
  const closeDonation = (value: boolean) => {
    setOpenSupport(value);
  };

  return (
    <UpFullWidthContainer>
      {openSupport && <Donate tokenName={creatorTokenName} closeDonation={closeDonation}></Donate>}

      <UpGap space="40px"></UpGap>
      <UpMainTitle userName={userState.user.name} location={profileState.location}></UpMainTitle>
      <UpGap space="20px"></UpGap>
      <UpContainer wt="100%">
        <UpRow>
          <UpContainer wt="50%">
            <UpProfileImg src={API_BASE_URL + "public/uploads/profiles/" + profileState.image}></UpProfileImg>
          </UpContainer>
          <UpContainer wt="50%">
            <UpRow>
              <UpAboutSubtitle>
                <h3>About</h3>
                <p>{profileState.profession}</p>
              </UpAboutSubtitle>
              <UpButton bg="#BE5FFF" onClick={goToDonationPage}>
                Donate
              </UpButton>
            </UpRow>
            <UpSocialsContainer>
              {profileState.social_media.map((item, index) => (
                <a href={item.link}>
                  <img src={twitchIcon} />
                </a>
              ))}
            </UpSocialsContainer>

            <UpRow>
              <UpContainerWidth wt="480px">
                <UpParagraph>{profileState.description}</UpParagraph>
              </UpContainerWidth>
            </UpRow>
            <UpGap space="20px"></UpGap>
            <UpRow>
              <UpNumberAbout>
                <h5>5</h5>
                <p>Number of Tokens Created</p>
                <h5>{profileState.events_held}</h5>
                <p>Events held</p>
                <h5>122K</h5>
                <p>Followers</p>
              </UpNumberAbout>
            </UpRow>
          </UpContainer>
        </UpRow>
      </UpContainer>
      <UpGap space="70px"></UpGap>
      <UpContainer wt="100%">
        <UpRow>
          <UpContainer wt="100%">
            <UpTitle>Coin Holder Benefits</UpTitle>
            <UpParagraph>{profileState.token_benefits}</UpParagraph>
          </UpContainer>
        </UpRow>
      </UpContainer>
    </UpFullWidthContainer>
  );
}
