import { Fragment, useState } from "react";
import { CardElement, useStripe, useElements } from "@stripe/react-stripe-js";
import { Box, Button, InputAdornment, InputLabel, OutlinedInput, TextField, Typography } from "@mui/material";
import { useFetchAndLoad, useWalletConnection } from "../../hooks";
import { TokenResult } from "@stripe/stripe-js";
import { sendStripePayment } from "../../services";
import { ProfileInterface, UserInterface } from "../../models/interfaces";
import { useSelector } from "react-redux";
import { AppStore } from "../../redux/store";
import { useParams } from "react-router";
import useFeedback from "../../hooks/useFeedback";
import { SupportProps } from "../../models/interfaces/SupportProps.interface";

export const CheckoutForm = ({ tokenName, closeDonation }: SupportProps) => {
  const [succeeded, setSucceeded] = useState(false);
  const [amount, setAmount] = useState(0);
  const [nameField, setNameField] = useState("");
  const [emailField, setEmailField] = useState("");
  const [error, setError] = useState(null);
  const [processing, setProcessing] = useState(false);
  const [disabled, setDisabled] = useState(true);
  const stripe = useStripe();
  const elements = useElements();
  const { loading, callEndpoint } = useFetchAndLoad();
  const userState: UserInterface = useSelector((store: AppStore) => store.user);
  const profileData: ProfileInterface = useSelector((store: AppStore) => store.profile);
  const { connectWallet, connectedWallet, connectButtonText } = useWalletConnection();
  const { userName = "" } = useParams();
  const { showPopup, showPopupWithToken } = useFeedback();
  const sendDataFormat = {
    amount: 0,
    source: "",
    username: "",
    name: "",
    email: "",
    supporterWallet: "",
  };

  const cardStyle = {
    style: {
      base: {
        color: "#32325d",
        fontFamily: "Arial, sans-serif",
        fontSmoothing: "antialiased",
        fontSize: "16px",
        "::placeholder": {
          color: "#32325d",
        },
      },
      invalid: {
        fontFamily: "Arial, sans-serif",
        color: "#fa755a",
        iconColor: "#fa755a",
      },
    },
  };

  const handleChange = async (event: any) => {
    setDisabled(event.empty);
    setError(event.error ? event.error.message : "");
  };

  const handleSubmit = async (ev: any) => {
    ev.preventDefault();
    setProcessing(true);
    const card = elements?.getElement(CardElement);
    if (card != null && stripe != null) {
      stripe.createToken(card).then(function (result) {
        sendPayment(result);
      });
    } else {
      closeDonation(false);
      setProcessing(false);
      showPopup("Oh no!", "There was an error, try again");
    }
  };

  const sendPayment = async (cardToken: TokenResult) => {
    if (amount != 0 && userName != "" && cardToken.token != undefined) {
      sendDataFormat.amount = amount;
      sendDataFormat.source = cardToken.token.id;
      sendDataFormat.username = userName;
      sendDataFormat.name = nameField;
      sendDataFormat.email = emailField;
      sendDataFormat.supporterWallet = connectedWallet;
      const formattedData = JSON.stringify(sendDataFormat);
      console.log(formattedData);
      try {
        await callEndpoint(sendStripePayment(formattedData), successCallback);
      } catch (e: any) {
        setProcessing(false);
        showPopup("Oh no!", e.message);
      }
    } else {
      closeDonation(false);
      setProcessing(false);
      showPopup("Oh no!", "Card not detected or user doesn't exist");
    }
  };

  const successCallback = (data: any) => {
    const tokenAddress = data.tokenTransfer.to;
    showPopupWithToken(
      "Awesome!",
      "Payment processed successfully, you will receive " +
        amount +
        " tokens to your wallet. If you don't see your tokens in your wallet please add it with the button below",
      tokenAddress
    );
    closeDonation(false);
    setProcessing(false);
  };

  return (
    <Fragment>
      <form id="payment-form" onSubmit={handleSubmit}>
        <Typography sx={{ mb: 3 }} variant="h4" align="center">
          Support {userState.user.name}
        </Typography>

        {connectedWallet ? (
          <>
            <Typography sx={{ mb: 3 }} variant="body1">
              By supporting {userState.user.name} you will receive some {tokenName} tokens.
            </Typography>
            <Typography sx={{ mb: 3 }} variant="body1">
              <Typography sx={{ fontWeight: "bold" }}> {tokenName} token allows you to: </Typography> {profileData.token_benefits}
            </Typography>
            <TextField
              margin="normal"
              required
              fullWidth
              id="name"
              label="Name"
              name="name"
              autoComplete="name"
              value={nameField}
              autoFocus
              onChange={(e) => setNameField(e.target.value)}
            />

            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              value={emailField}
              autoComplete="email"
              onChange={(e) => setEmailField(e.target.value)}
            />
            <InputLabel htmlFor="outlined-adornment-amount">Amount</InputLabel>

            <OutlinedInput
              value={amount}
              onChange={(e) => setAmount(parseInt(e.target.value))}
              startAdornment={<InputAdornment position="start">$</InputAdornment>}
              label="Amount"
              type="number"
              className="numberInput"
            />
            <CardElement id="card-element" options={cardStyle} onChange={handleChange} />
            <Typography sx={{ marginTop: 2 }}>
              Wallet Connected: <span className="short-wallet">{connectedWallet}</span>
            </Typography>
            <Box
              m={1}
              //margin
              display="flex"
              justifyContent="center"
              alignItems="center"
            >
              <Button
                sx={{ marginTop: 2, marginRight: 2 }}
                className="pay-button"
                color="error"
                variant="contained"
                onClick={() => closeDonation(false)}
              >
                <span id="button-text">Cancel</span>
              </Button>
              <Button
                sx={{ marginTop: 2 }}
                className="pay-button"
                disabled={processing || disabled || succeeded}
                id="submit"
                color="primary"
                variant="contained"
                type="submit"
              >
                <span id="button-text">{processing ? <div className="spinner" id="spinner"></div> : "Donate"}</span>
              </Button>
            </Box>
            <Box
              m={1}
              //margin
              display="flex"
              justifyContent="center"
              alignItems="center"
            >
              {/* Show any error that happens when processing the payment */}
              {error && (
                <div className="card-error" role="alert">
                  {error}
                </div>
              )}
              {/* Show a success message upon completion */}
              <p className={succeeded ? "result-message" : "result-message hidden"}>
                Payment succeeded, see the result in your
                <a href={`https://dashboard.stripe.com/test/payments`}> Stripe dashboard.</a> Refresh the page to pay again.
              </p>
            </Box>
          </>
        ) : (
          <Box textAlign="center">
            <Typography> Please connect your wallet to support {userState.user.name}</Typography>
            <Button
              sx={{ marginTop: 2, marginRight: 2 }}
              className="pay-button"
              color="error"
              variant="contained"
              onClick={() => closeDonation(false)}
            >
              <span id="button-text">Cancel</span>
            </Button>
            <Button sx={{ marginTop: 2 }} className="pay-button" color="primary" variant="contained" onClick={connectWallet}>
              <span id="button-text">{connectButtonText}</span>
            </Button>
          </Box>
        )}
      </form>
    </Fragment>
  );
};
