import { useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import { Box, Button, Divider, Drawer, Theme, Typography, useMediaQuery } from "@mui/material";
import OpenInNewIcon from "@mui/icons-material/OpenInNew";
import { ChartBar as ChartBarIcon } from "../../icons/chart-bar";

import { Cog as CogIcon } from "../../icons/cog";
import { Lock as LockIcon } from "../../icons/lock";
import { Selector as SelectorIcon } from "../../icons/selector";
import { ShoppingBag as ShoppingBagIcon } from "../../icons/shopping-bag";
import { User as UserIcon } from "../../icons/user";
import { UserAdd as UserAddIcon } from "../../icons/user-add";
import { Users as UsersIcon } from "../../icons/users";
import { XCircle as XCircleIcon } from "../../icons/x-circle";
import { NavItem } from "./nav-item";
import { useDispatch, useSelector } from "react-redux";
import { UserInterface } from "../../models/interfaces";
import { AppStore } from "../../redux/store";
import { logOut } from "../../redux/states/user";

const items = [
  {
    href: "/creator-tokens",
    icon: <ChartBarIcon fontSize="small" />,
    title: "Tokens",
  },

  {
    href: "/creator-payment",
    icon: <ShoppingBagIcon fontSize="small" />,
    title: "Payment",
  },
  {
    href: "/creator-profile",
    icon: <UsersIcon fontSize="small" />,
    title: "Profile",
  },
];

export const DashboardSidebar = (props: any) => {
  const userState: UserInterface = useSelector((store: AppStore) => store.user);
  const dispatch = useDispatch();
  const { open, onClose } = props;
  const navigate = useNavigate();
  const lgUp = useMediaQuery((theme: Theme) => theme.breakpoints.up("lg"), {
    defaultMatches: true,
    noSsr: false,
  });

  const logoutUser = () => {
    dispatch(logOut());
    localStorage.setItem("authtoken", "");
    localStorage.setItem("username", "");
    sessionStorage.setItem("authtoken", "");
    sessionStorage.setItem("username", "");
    navigate("/login");
  };

  const content = (
    <>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          height: "100%",
        }}
      >
        <div>
          <Box sx={{ p: 3 }}>
            <a></a>
          </Box>
          <Box sx={{ px: 2 }}>
            <Box
              sx={{
                alignItems: "center",
                backgroundColor: "rgba(255, 255, 255, 0.04)",
                cursor: "pointer",
                display: "flex",
                justifyContent: "space-between",
                px: 3,
                py: "11px",
                borderRadius: 1,
              }}
            >
              <div>
                <Typography color="inherit" variant="subtitle1">
                  {userState.user.username}
                </Typography>
                <Typography color="neutral.400" variant="body2">
                  Content Creator
                </Typography>
              </div>
            </Box>
          </Box>
        </div>
        <Divider
          sx={{
            borderColor: "#2D3748",
            my: 3,
          }}
        />
        <Box sx={{ flexGrow: 1 }}>
          {items.map((item) => (
            <NavItem key={item.title} icon={item.icon} href={item.href} title={item.title} />
          ))}
        </Box>
        <Divider sx={{ borderColor: "#2D3748" }} />
        <Box
          sx={{
            px: 2,
            py: 3,
          }}
        >
          <Button
            color="secondary"
            component="a"
            endIcon={<OpenInNewIcon />}
            fullWidth
            sx={{ mt: 2 }}
            variant="contained"
            onClick={logoutUser}
          >
            Logout
          </Button>
        </Box>
      </Box>
    </>
  );

  if (lgUp) {
    return (
      <Drawer
        anchor="left"
        open
        PaperProps={{
          sx: {
            backgroundColor: "neutral.900",
            color: "#FFFFFF",
            width: 280,
          },
        }}
        variant="permanent"
      >
        {content}
      </Drawer>
    );
  }

  return (
    <Drawer
      anchor="left"
      onClose={onClose}
      open={open}
      PaperProps={{
        sx: {
          backgroundColor: "neutral.900",
          color: "#FFFFFF",
          width: 280,
        },
      }}
      sx={{ zIndex: (theme) => theme.zIndex.appBar + 100 }}
      variant="temporary"
    >
      {content}
    </Drawer>
  );
};

DashboardSidebar.propTypes = {
  onClose: PropTypes.func,
  open: PropTypes.bool,
};
