import { Snackbar, SnackbarContent } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { modifySnackBar } from "../../redux/states/snackbar";
import { AppStore } from "../../redux/store";

export const SnackBarMessage = () => {
  const snackbarState = useSelector((store: AppStore) => store.snackbar);
  const dispatcher = useDispatch();

  const closeSnackBar = () => {
    dispatcher(modifySnackBar({ opened: false }));
  };

  return (
    <div>
      <Snackbar
        open={snackbarState.opened}
        autoHideDuration={6000}
        onClose={closeSnackBar}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      >
        <SnackbarContent
          style={{
            backgroundColor: "#48e5af",
            textAlign: "center",
          }}
          message={<span>{snackbarState.message}</span>}
        />
      </Snackbar>
    </div>
  );
};
