import styled from "styled-components";

interface Props {
  wt: string;
}

export const UpContainer = styled.div<Props>`
  width: ${({ wt }: any) => wt || "100%"};
  max-width: 1662px;
  padding: 0 30px;
  margin: 0 auto;
`;

export const UpFullWidthContainer = styled.div`
  width: 100%;
`;
