import axios from "axios";
import { API_BASE_URL, loadAbort } from "./../utilities";

const baseUrl: string = API_BASE_URL;

export const sendStripePayment = (data: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
  };
  return {
    call: axios.post(baseUrl + "payment/makePayment", data, { headers: headers, signal: controller.signal }),
    controller,
  };
};

export const saveAccountData = (data: string, authToken: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
    Authorization: "Bearer " + authToken,
  };
  return {
    call: axios.post(baseUrl + "payment/createUserAccount", data, { headers: headers, signal: controller.signal }),
    controller,
  };
};

export const getAccountData = (authToken: string) => {
  const controller = loadAbort();
  const headers = {
    "Content-Type": "application/json",
    Authorization: "Bearer " + authToken,
  };
  return {
    call: axios.get(baseUrl + "payment/getBankAccount", { headers: headers, signal: controller.signal }),
    controller,
  };
};