import { useDispatch } from "react-redux";
import { Outlet } from "react-router";
import { UserInterface } from "../models/interfaces";
import { Login } from "../pages/Login";
import { modifyUser } from "../redux/states/user";
import store from "../redux/store";

const useAuth = () => {
  let userTokenInStorage = sessionStorage.getItem("authtoken");
  let userNameInStorage = sessionStorage.getItem("username");
  if (localStorage.getItem("authtoken") && localStorage.getItem("username")) {
    userTokenInStorage = localStorage.getItem("authtoken");
    userNameInStorage = localStorage.getItem("username");
  }
  const dispatch = useDispatch();
  let userLoggedIn: boolean = false;

  if (userTokenInStorage && userNameInStorage) {
    userLoggedIn = true;
    dispatch(modifyUser({ token: userTokenInStorage, user: { username: userNameInStorage } }));
  }
  const userState: UserInterface = store.getState().user;
  //const userState: UserInterface = useSelector((store: AppStore) => store.user);
  if (userState.token) {
    userLoggedIn = true;
  }
  return userLoggedIn;
};

const ProtectedRoutes = () => {
  const isAuth: boolean = useAuth();
  return isAuth ? <Outlet /> : <Login />;
};

export default ProtectedRoutes;
