import titleDivider from "../../assets/titleDivider.png";
import vectorLocation from "../../assets/vector_location.png";
import { StyledUpMainTitle } from "../../styled-components";

interface MainTitleProps {
  userName: string;
  location: string;
}

export const UpMainTitle = ({ userName, location }: MainTitleProps) => {
  return (
    <StyledUpMainTitle>
      <div>
        <h2>{userName}</h2>
      </div>
      <div className="divider">
        <img src={titleDivider} />
      </div>

      <div className="location_container">
        <img src={vectorLocation} />
        <p>{location}</p>
      </div>
    </StyledUpMainTitle>
  );
};
